.create-fund-form button[type=submit] {
    background-color: var(--button-color);
    color: white;
    width: 150px;
    height: 40px;
    border-radius: 12px;
    outline: none;
    border: none;
    margin: 30px auto;
    transition: all 0.3s;
    box-shadow: 0 0 3px 3px var(--button-color);
}

.create-fund-form button[type=submit]:hover {
    color: var(--button-color);
    background-color: white;
}

.create-fund-form button[type=submit]:disabled {
    background-color: var(--background-color);
    opacity: 0.7;
    cursor: not-allowed;
}

